<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Registrar nuevo eto:</h4>

		<b-form>
			<b-form-group label="*Campo"
						  label-for="id_campo"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_campo"
								v-model="eto.id_campo"
								:items="opcampos"
								placeholder="Elija el campo"
								:state="validacion.id_campo.estado"
								invalid-feedback="Complete este campo"
								required
								solo
								filled>
				</v-autocomplete>
				<b-form-invalid-feedback id="id_campo-live-feedback">
					{{ validacion.id_campo.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<!-- nro_ticket -->
			<b-form-group label="*Fecha"
						  label-for="fecha"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="fecha"
							  v-model="eto.fecha"
							  type="date"
							  placeholder="Ingrese el numero del patente"
							  :state="validacion.fecha.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="fecha-live-feedback">
					{{ validacion.fecha.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Eto"
						  label-for="eto"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="eto"
							  v-model="eto.eto"
							  type="number"
							  placeholder="Ingrese el numero de eto"
							  :state="validacion.eto.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="eto-live-feedback">
					{{ validacion.eto.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button
			class="mt-2"
			variant="success"
			block
			@click="postEto()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				eto: {},
				data: {},
                opcampos: [
                    { value: null, text: "Elija un campo", disabled: true },
                ],
				validacion: {
                    id_campo: { estado: null, mensaje: "" },
					fecha: { estado: null, mensaje: "" },
                    eto: { estado: null, mensaje: "" },
				},
				respuesta: {},
			};
		},
		methods: {
			async getEto() {
                let EtoAPI = new APIControler();
                this.data = await EtoAPI.getData();
			},
			async postEto() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "eto/";
				this.respuesta = await gastoSalienteAPI.postData(this.eto);
				this.cargarFeedback();
				this.updateTable();
			},
            fechaactual() {
                const f = new Date();
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
			},
            async getCampos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "campos/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://45.162.168.170:8081/campos/" + element.id_campo + "/";
                    option.text = element.id_campo + ' - ' + element.nombre + ' - ' + element.calle;
                    this.opcampos.push(option);
                });
            },
			cargarFeedback() {
				let valido;
				this.respuesta=this.respuesta|| {}
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
            inicializar() {
                this.eto.fecha = this.fechaactual();
            },
		},
        beforeMount() {
			this.inicializar();
			this.getCampos();
        },
	};
</script>

<style></style>
